@font-face {
  font-family: AdidasFont;
  src: url(../assets/fonts/AdihausDIN.ttf);
}
@font-face {
  font-family: AdidasFontBold;
  src: url(../assets/fonts/AdihausDIN-bold.ttf);
}
@font-face {
  font-family: ClashGroteskExtraLight;
  src: url(../assets/fonts/ClashGrotesk-Extralight.otf);
}
@font-face {
  font-family: ClashGroteskLight;
  src: url(../assets/fonts/ClashGrotesk-Light.otf);
}
@font-face {
  font-family: ClashGroteskRegular;
  src: url(../assets/fonts/ClashGrotesk-Regular.otf);
}
@font-face {
  font-family: ClashGroteskMedium;
  src: url(../assets/fonts/ClashGrotesk-Medium.otf);
}
@font-face {
  font-family: ClashGroteskSemiBold;
  src: url(../assets/fonts/ClashGrotesk-Semibold.otf);
}
@font-face {
  font-family: ClashGroteskBold;
  src: url(../assets/fonts/ClashGrotesk-Bold.otf);
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  letter-spacing: 0.05rem;
  word-break: break-word;
}

body {
  width: 100%;
}

main {
  width: 100%;
}

#root {
  margin-left: 15rem;
  padding: 1rem;
}
@media (max-width: 48rem) {
  #root {
    margin-left: 0rem;
  }
}

h1 {
  font-family: ClashGroteskBold, sans-serif, Arial, Helvetica;
}

h2 {
  font-family: ClashGroteskSemiBold, sans-serif, Arial, Helvetica;
}

h3 {
  font-family: ClashGroteskMedium, sans-serif, Arial, Helvetica;
}

h4,
h5,
h6,
p {
  font-family: ClashGroteskRegular, sans-serif, Arial, Helvetica;
}

a {
  font-family: ClashGroteskRegular, sans-serif, Arial, Helvetica;
  text-decoration: none;
  color: black;
}

.nav-bold {
  font-family: ClashGroteskSemiBold, sans-serif, Arial, Helvetica;
}

nav {
  font-family: ClashGroteskRegular, sans-serif, Arial, Helvetica;
  position: fixed;
  top: 0;
  left: 0;
  width: 15rem;
  height: 100%;
  background-color: rgb(74, 45, 71);
  color: #f4e9e9;
  padding-top: 1rem;
  padding-left: 1rem;
  padding-right: 1rem;
  transition: left 0.3s;
}
@media (max-width: 48rem) {
  nav {
    left: -15rem;
    z-index: 1;
  }
}
nav > div {
  margin: 0.5rem 0;
  transition: background-color 0.3s, border-radius 0.3s;
}
nav > div:hover {
  cursor: pointer;
  background-color: rgb(155, 94, 149);
  border-radius: 0.5rem;
}
nav > div > a {
  display: block;
  width: 100%;
  height: 100%;
  padding: 0.5rem 0.2rem;
  text-decoration: none;
  color: #f4e9e9;
}
nav > div > a i {
  margin-right: 1rem;
}
nav .library {
  padding-right: 1rem;
}
nav .sub-library {
  padding-right: 1rem;
  padding-left: 1rem;
}
nav .library,
nav .sub-library {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
nav hr {
  width: 100%;
  color: #f4e9e9;
}

.close-btn {
  display: none;
}
@media (max-width: 48rem) {
  .close-btn {
    display: block;
    position: absolute;
    right: 5px;
    top: -5px;
  }
}
.close-btn:hover {
  cursor: pointer;
}

.hamburger-clicked {
  left: 0;
}

.hamburger {
  display: none;
  margin-top: 1rem;
  margin-left: 1rem;
}
@media (max-width: 48rem) {
  .hamburger {
    display: block;
  }
}
.hamburger i {
  border: 1px solid black;
  padding: 2px;
}
.hamburger i:hover {
  cursor: pointer;
}

.home-page > h1 {
  font-family: ClashGroteskBold;
  font-size: 3rem;
  margin-bottom: 5rem;
  text-transform: uppercase;
  text-align: center;
}
@media (max-width: 26.25rem) {
  .home-page > h1 {
    font-size: 1rem;
  }
}
.home-page .nyt-category-card:first-of-type > h2 {
  font-family: ClashGroteskMedium;
  margin-top: 0.5rem;
}

.nyt-category-card > h2 {
  font-family: ClashGroteskMedium;
  text-align: center;
  font-size: 2rem;
  margin-top: 3rem;
}
@media (max-width: 48rem) {
  .nyt-category-card > h2 {
    font-size: 1.5rem;
  }
}
@media (max-width: 26.25rem) {
  .nyt-category-card > h2 {
    font-size: 1rem;
  }
}
.nyt-category-card > div {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  column-gap: 1rem;
  margin-top: 0rem;
  overflow-x: auto;
}
@media (max-width: 26.25rem) {
  .nyt-category-card > div {
    grid-template-columns: 1fr;
    row-gap: 1rem;
  }
}

.nyt-book-card a {
  font-family: ClashGroteskRegular;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  text-decoration: none;
  border: 1px solid rgb(201, 197, 197);
  padding: 0.5rem 0.5rem;
  transition: border 0.3s;
}
@media (max-width: 26.25rem) {
  .nyt-book-card a {
    margin-inline: auto;
  }
}
.nyt-book-card a:hover {
  border-color: rgb(155, 94, 149);
}
.nyt-book-card a > div {
  height: 20rem;
  width: 100%;
  margin-bottom: 1rem;
  display: flex;
  justify-content: center;
}
@media (max-width: 90rem) {
  .nyt-book-card a > div {
    width: 60%;
    margin-bottom: 0.3rem;
    height: 10rem;
  }
}
@media (max-width: 64rem) {
  .nyt-book-card a > div {
    width: 60%;
    margin-bottom: 0.3rem;
    height: 8rem;
  }
}
@media (max-width: 529px) {
  .nyt-book-card a > div {
    width: 60%;
    margin-bottom: 0.3rem;
    height: 6rem;
  }
}
@media (max-width: 26.25rem) {
  .nyt-book-card a > div {
    width: 60%;
    margin-bottom: 0.3rem;
    height: 8rem;
  }
}
.nyt-book-card a > div img {
  width: auto;
  height: 100%;
}
.nyt-book-card a > p {
  text-align: center;
  color: black;
}
@media (max-width: 48rem) {
  .nyt-book-card a > p {
    font-size: 0.7rem;
  }
}
@media (max-width: 26.25rem) {
  .nyt-book-card a > p {
    font-size: 0.6rem;
  }
}
.nyt-book-card a > p:first-of-type {
  font-family: ClashGroteskSemiBold;
}
.nyt-book-card a > p:nth-of-type(2) {
  font-family: ClashGroteskLight;
}

.search-page .search-area {
  margin-top: 5rem;
  margin-bottom: 2rem;
  width: min(90%, 90rem);
  margin-inline: auto;
  word-wrap: break-word;
}
.search-page .search-results {
  margin-top: 2rem;
  margin-bottom: 5rem;
  width: min(90%, 90rem);
  margin-inline: auto;
  word-wrap: break-word;
}

#search-form > label {
  font-size: 2rem;
}
@media (max-width: 26.25rem) {
  #search-form > label {
    font-size: 1.3rem;
    margin-bottom: 1rem;
  }
}
@media (max-width: 26.25rem) {
  #search-form > h2 {
    font-size: 1rem;
    margin-top: 1rem;
    margin-bottom: 0.5rem;
    font-family: ClashGroteskRegular, sans-serif, Arial, Helvetica;
  }
}
#search-form > div {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
@media (max-width: 48rem) {
  #search-form > div {
    flex-direction: column;
  }
}
#search-form > div input {
  width: 50%;
  font-family: ClashGroteskRegular, sans-serif, Arial, Helvetica;
  font-size: 1.5rem;
  outline: none;
  padding: 0.3rem 0.1rem;
  height: 3rem;
  margin-right: 1rem;
  border: 2px solid black;
  border-radius: 5px;
}
@media (max-width: 48rem) {
  #search-form > div input {
    width: 100%;
    margin-bottom: 1rem;
    margin-right: 0;
    font-size: 1.5rem;
  }
}
#search-form > div button {
  width: 15rem;
  font-size: 1.5rem;
  padding: 0.3rem 0.1rem;
  height: 3rem;
  outline: none;
  border-radius: 0px;
  border: 2px solid #f4e9e9;
  color: rgb(74, 45, 71);
  transition: background-color 0.2s, color 0.2s;
}
@media (max-width: 48rem) {
  #search-form > div button {
    width: 100%;
    font-size: 1.3rem;
  }
}
#search-form > div button:hover {
  background-color: rgb(74, 45, 71);
  cursor: pointer;
  color: #f4e9e9;
}

@media (max-width: 26.25rem) {
  .search-results h2 {
    font-size: 1rem;
    font-family: ClashGroteskRegular, sans-serif, Arial, Helvetica;
  }
}

.book-obj-card {
  position: relative;
  border: 1px solid rgb(201, 197, 197);
  border-radius: 1rem;
  padding: 1rem 5rem;
  padding-bottom: 5rem;
  margin-bottom: 3rem;
  display: grid;
  grid-template-columns: auto 1fr;
  column-gap: 2rem;
  transition: border-color 0.2s;
}
@media (max-width: 64rem) {
  .book-obj-card {
    grid-template-columns: 1fr;
  }
}
@media (max-width: 26.25rem) {
  .book-obj-card {
    padding: 1rem 1rem;
    padding-bottom: 5rem;
  }
}
.book-obj-card__img {
  display: block;
  height: 20rem;
  border: 1px solid rgb(201, 197, 197);
}
@media (max-width: 64rem) {
  .book-obj-card__img {
    height: 15rem;
    width: 100%;
    border: none;
    display: flex;
    justify-content: center;
  }
}
@media (max-width: 26.25rem) {
  .book-obj-card__img {
    height: 10rem;
  }
}
@media (max-width: 64rem) {
  .book-obj-card__img a {
    display: flex;
    justify-content: center;
    height: 100%;
    width: 0%;
  }
}
.book-obj-card__img a img {
  height: 100%;
  margin-inline: auto;
}
@media (max-width: 26.25rem) {
  .book-obj-card__img a img {
    border: 1px solid rgb(201, 197, 197);
  }
}
.book-obj-card__info {
  height: 100%;
}
.book-obj-card__info > h2 {
  font-size: 2.5rem;
  margin-bottom: 0.1rem;
}
@media (max-width: 64rem) {
  .book-obj-card__info > h2 {
    text-align: center;
    margin-top: 1rem;
    font-size: 2rem;
  }
}
@media (max-width: 26.25rem) {
  .book-obj-card__info > h2 {
    font-size: 1.5rem;
  }
}
.book-obj-card__info > p {
  font-size: 1.5rem;
  margin-bottom: 2rem;
}
@media (max-width: 64rem) {
  .book-obj-card__info > p {
    text-align: center;
    font-size: 1.3rem;
  }
}
@media (max-width: 26.25rem) {
  .book-obj-card__info > p {
    font-size: 1rem;
  }
}
.book-obj-card__info > div {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1rem;
}
@media (max-width: 26.25rem) {
  .book-obj-card__info > div {
    grid-template-columns: 1fr;
    grid-gap: 0.5rem;
    padding: 0 2rem;
  }
}
.book-obj-card__info > div button {
  padding: 0.5rem;
  font-size: 1rem;
  background-color: white;
  border: 1px solid rgb(201, 197, 197);
  border-radius: 1rem;
  color: rgb(74, 45, 71);
  transition: background-color 0.2s, color 0.2s;
}
.book-obj-card__info > div button:hover {
  cursor: pointer;
  background-color: rgb(155, 94, 149);
  color: white;
}
.book-obj-card__info > div button > i:nth-of-type(2) {
  display: none;
}
@media (max-width: 1196px) {
  .book-obj-card__info > div button > i:nth-of-type(2) {
    display: inline;
  }
}
@media (max-width: 1196px) {
  .book-obj-card__info > div button > span {
    display: none;
  }
}
.book-obj-card > a {
  position: absolute;
  display: block;
  right: 20px;
  bottom: 10px;
  padding: 0.3rem;
  border-radius: 5rem;
  transition: background-color 0.2s, color 0.2s;
}
.book-obj-card > a:hover {
  cursor: pointer;
  background-color: rgb(155, 94, 149);
  color: white;
}
.book-obj-card:hover {
  border-color: rgb(155, 94, 149);
}

.sub-lib-book-card {
  height: 100%;
  word-wrap: normal;
  word-break: normal;
}
.sub-lib-book-card > a {
  font-family: ClashGroteskRegular;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  text-decoration: none;
  border: 1px solid rgb(201, 197, 197);
  padding: 0.5rem 0.5rem;
  transition: border 0.3s;
  word-wrap: normal;
  word-break: normal;
}
@media (max-width: 26.25rem) {
  .sub-lib-book-card > a {
    margin-inline: auto;
  }
}
.sub-lib-book-card > a:hover {
  border-color: rgb(155, 94, 149);
}
.sub-lib-book-card > a > div {
  height: 20rem;
  width: 100%;
  margin-bottom: 1rem;
  display: flex;
  justify-content: center;
}
@media (max-width: 90rem) {
  .sub-lib-book-card > a > div {
    width: 60%;
    margin-bottom: 0.3rem;
    height: 10rem;
  }
}
@media (max-width: 64rem) {
  .sub-lib-book-card > a > div {
    width: 60%;
    margin-bottom: 0.3rem;
    height: 8rem;
  }
}
@media (max-width: 529px) {
  .sub-lib-book-card > a > div {
    width: 60%;
    margin-bottom: 0.3rem;
    height: 6rem;
  }
}
@media (max-width: 26.25rem) {
  .sub-lib-book-card > a > div {
    width: 60%;
    margin-bottom: 0.3rem;
    height: 8rem;
  }
}
.sub-lib-book-card > a > div img {
  width: auto;
  height: 100%;
}
.sub-lib-book-card > a > h3 {
  word-wrap: normal;
  word-break: normal;
  text-align: center;
  color: black;
}
@media (max-width: 48rem) {
  .sub-lib-book-card > a > h3 {
    font-size: 1.2rem;
  }
}
@media (max-width: 26.25rem) {
  .sub-lib-book-card > a > h3 {
    font-size: 1rem;
  }
}
.sub-lib-book-card > a > p {
  text-align: center;
  color: black;
}
@media (max-width: 48rem) {
  .sub-lib-book-card > a > p {
    font-size: 0.7rem;
  }
}
@media (max-width: 26.25rem) {
  .sub-lib-book-card > a > p {
    font-size: 0.6rem;
  }
}

.sub-lib-card {
  position: relative;
  margin-bottom: 2rem;
}
.sub-lib-card > h2 {
  margin-bottom: 1rem;
}
@media (max-width: 48rem) {
  .sub-lib-card > h2 {
    font-size: 1.5rem;
  }
}
@media (max-width: 26.25rem) {
  .sub-lib-card > h2 {
    font-size: 1.2rem;
  }
}
.sub-lib-card__books {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  column-gap: 1rem;
  overflow-x: auto;
  box-shadow: 2px 0px 5px rgba(0, 0, 0, 0.2), -2px 0px 5px rgba(0, 0, 0, 0.2);
}
.sub-lib-card__empty {
  color: black;
}
@media (max-width: 48rem) {
  .sub-lib-card__empty {
    font-size: 1rem;
  }
}
@media (max-width: 26.25rem) {
  .sub-lib-card__empty {
    font-size: 0.8rem;
  }
}
.sub-lib-card > a {
  position: absolute;
  right: 0;
  top: 0;
  display: inline-block;
  padding: 0.3rem;
  border-radius: 5rem;
  color: black;
  transition: background-color 0.2s, color 0.2s;
}
@media (max-width: 48rem) {
  .sub-lib-card > a {
    font-size: 0.7rem;
  }
}
@media (max-width: 26.25rem) {
  .sub-lib-card > a {
    font-size: 0.6rem;
    position: static;
    margin-top: 0.3rem;
  }
}
.sub-lib-card > a:hover {
  cursor: pointer;
  background-color: rgb(155, 94, 149);
  color: white;
}

.library-page {
  margin-top: 1rem;
  margin-bottom: 2rem;
  width: min(90%, 90rem);
  margin-inline: auto;
  word-wrap: break-word;
}
.library-page > h1 {
  margin-bottom: 1rem;
}
@media (max-width: 48rem) {
  .library-page > h1 {
    font-size: 2rem;
  }
}
@media (max-width: 26.25rem) {
  .library-page > h1 {
    font-size: 1.6rem;
  }
}

